	import uniIcons from '@/components/uni-icons/uni-icons.vue'
	export default {
		components: {uniIcons},
		data() {
			return {
				login: false,
				userInfo: {},
				banImg: "", //海报图
			}
		},
		onShow() {
			uni.getStorage({
				key: "user",
				success: ok => {
					this.login = true;
					this.getUserInfo();
				},
				fail: err => {
					this.login = false;
				}
			})
		},
		onLoad(){
			this.getBanner();
		},
		methods: {
			getBanner() {
				this.api({
					method: "get",
					url: "User/poster"
				}).then(res=> {
					console.log(res);
					this.banImg = res.data.data;
				})
			},
			getUserInfo() {
				this.api({
					method: "post",
					url: "User/user_info"
				}).then(res=> {
					// console.log(res);
					this.userInfo = res.data.date;
					uni.setStorage({
						key: "vip",
						data: res.data.date.vip
					})
				})
			},
			getInfo(e) {
				const userInfo = e.detail.userInfo;
				const nickName = userInfo.nickName;
				const avatarUrl = userInfo.avatarUrl;
				// const a = JSON.stringify(userInfo);
				uni.login({
					provider: 'weixin',
					success:  (loginRes)=> {
					  // console.log(loginRes);
					  const code = loginRes.code;
					  this.api({
						 method: "get",
						 url: "Index/register",
						 data: {
							 code: code,
							 nickName: nickName,
							 avatarUrl: avatarUrl
						 }
					  }).then(res=> {
						  // console.log(res);
						  const user = res.data.date;
						  uni.setStorage({
						  	key: "user",
							data: user,
							success: r=> {
								this.getUserInfo();
								uni.showToast({
									title: '授权登录成功',
									duration:1000,
									icon: 'success'
								})
								setTimeout(()=> {
									this.login = true; //登录成功
								},1000)
							}
						  })
						  // uni.setStorageSync('user', user);
						  // this.getUserInfo();
					  })
					}
					
				})
			},
			logout() {
				uni.removeStorage({
					key: "user",
					success: res=> {
						this.login = false;
					}
				})
			},
			toPage(url) {
				uni.navigateTo({
					url: url
				})
			}
		}
 	}
